body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: auto;
  display: block;
}

.card__title {
  color: inherit;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0;
  padding: 4px 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews-block {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  padding-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: inherit;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__value {
  justify-content: space-between;
  padding-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: #000;
  align-items: end;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.0aa15587.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.1a24ce14.svg");
}
